.sidebar {
	display: flex;
	flex-direction: column;
	gap: 40px;
}

.sidebar img {
	width: 40px;
	height: 40px;
	cursor: pointer;
}

.sidebar_list {
	display: flex;
	flex-direction: column;
	gap: 20px;
	align-items: center;
	height: 0;
	transition: 300ms;
}

.sidebar_list_active {
	height: 300px;
}
.sidebar_list_item {
	border-radius: 50%;
	height: 26px;
	width: 26px;
	list-style: none;
}
